@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(//db.onlinewebfonts.com/c/af1155f85c206cbe07c7774b7ce884e8?family=ModestoW00-Initials);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  -webkit-font-smoothing: antialiased
}

button {
  cursor: pointer
}
.containerWrapper {
  width: 100%;
  height: 20%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.imgcontainer {
  height: 100vh;
  width: 100%;
  background-image: url(/static/media/fire.e758ca0a.jpeg);
  background-size: cover;
  background-position: center;
  box-shadow: 0px 5px 10px 0px rgba(138, 149, 158, 0.15);
}

.headerwrapper {
  /* background-color: #423d33f5; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  width: 200%;
  /* max-width: 750px; */
  height: auto;
  object-fit: cover;
}

.apresentantion {
  padding: 64px 0;
}

.apresentantion h1 {
  font-family: "ModestoW00-Initials";
  color: #ee4036;
  font-size: 27px;
  cursor: default;
}

.divisor {
  width: 64px;
  height: 3px;
  border-radius: 100rem;
  background-color: #faac18;
  margin: 32px auto;
}

.apresentantion p {
  font-family: 'Lato', sans-serif;
  width: 60%;
  margin: auto;
  font-size: 13px;
  color: #12113d;
  font-weight: 500;
}

.arrow {
  position: absolute;
  bottom: 64px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.arrow span {
  display: block;
  width: 10px;
  height: 10px;
  border-bottom: 3px solid #fff;
  border-right: 3px solid #fff;
  transform: rotate(45deg);
  margin: -10px;
  -webkit-animation: animate 2s infinite;
          animation: animate 2s infinite;
}

.arrow span:nth-child(2) {
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}

.arrow span:nth-child(3) {
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

@-webkit-keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}
.productList {
  display: flex;
  padding: 0 32px;
}

.cartBtnContainer {
  position: fixed;
  bottom: 20px;
  left: calc(-50vw + 50);
}

.cartBtn {
  width: 300px;
  height: 50px;
  border-radius: 5px;
  /*background-color: #12113d;
  border-color: #12113d;*/
  background-color: #faac18;
  border: none;
  cursor: pointer;
  color: #FFF;
  font-family: "ModestoW00-Initials";
  font-size: 14px;
  transition: 0.2s box-shadow, 0.2s -webkit-filter;
  transition: 0.2s box-shadow, 0.2s filter;
  transition: 0.2s box-shadow, 0.2s filter, 0.2s -webkit-filter;
}

.cartBtn:hover {
  -webkit-filter: brightness(95%);
          filter: brightness(95%);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cartIcon {
  margin-right: 8px;
}

.products {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.productWrapper {
  border-radius: 7px;
  height: 100%;
  box-shadow: 0px 5px 10px 0px rgba(138, 149, 158, 0.15);
  background-color: #0b083c;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.productContent {
  padding: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  flex-direction: column;
}

.product-item {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}

.productContentWrapper {
  display: flex;
  height: 100%;
}

.productHeader {
  height: 40% !important;
  min-height: 40%;
}

.product-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 7px 7px 0 0;
}

.productPrice {
  width: 100%;
}

strong {
  font-size: 16px;
  /*color: #ee4036;*/
  color: #faac18;
  width: 100%;
}

.additional {
  cursor: not-allowed;
}

.productInfo {
  color: #fff;
  flex: 1 1;
}

.productPrice {
  color: #fff;
  font-weight: bold;
}

.buttonAdd {
  border: none;
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 13px;
  color: #fff;
  background-color: #faac18;
  outline: none !important;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.actionSheet {
  display: flex;
  justify-content: space-around;
