.editButton {
  border: none;
  background-color: #fff;
  height: 26px;
  width: 26px;
  border-radius: 13px;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 52px;
  right: -10px;
  outline: none!important;
}

.aditionalOpt {
  width: 100%;
  border: none;
  height: 28px;
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 5px;
  display: flex;
  padding: 0 12px;
  justify-content: space-between;
  align-items: center;
  outline: none !important;
}

.spanName {
  width: 100%;
  display: flex;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.spanName span {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
